@import url('https://fonts.googleapis.com/css?family=Montserrat');

.profilecard{
    display: flex;
    flex-wrap: wrap;
}
.text {
	margin: 10px 0;
    color : #B3B8CD;
}

h6 {
	margin: 5px 0;
	text-transform: uppercase;
}

.card-title {
	font-size: 14px;
	line-height: 21px;
}

.card-container {
	background-color: #0050b3;
	border-radius: 5px;
	box-shadow: 0px 10px 20px -10px rgba(0,0,0,0.75);
	color: #B3B8CD;
	padding-top: 30px;
	position: relative;
    width: 350px;
	max-width: 100%;
	text-align: center;
    margin: 15px;
}

.card-container .pro {
	color: #231E39;
	background-color: #FEBB0B;
	border-radius: 3px;
	font-size: 14px;
	font-weight: bold;
	padding: 3px 7px;
	position: absolute;
	top: 30px;
	left: 30px;
}

.card-container .round {
	border: 1px solid #03BFCB;
	border-radius: 50%;
	padding: 7px;
}

button.primary {
	background-color: #03BFCB;
	border: 1px solid #03BFCB;
	border-radius: 3px;
	color: #231E39;
	font-family: Montserrat, sans-serif;
	font-weight: 500;
	padding: 10px 25px;
    margin-left: 20px;
}

button.primary.ghost {
	background-color: transparent;
	color: #02899C;
}

.skills {
	background-color: #002766;
	text-align: left;
	padding: 15px;
	margin-top: 30px;
}

.skills ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.skills ul li {
	border: 1px solid #02899C;
	border-radius: 2px;
	display: inline-block;
	font-size: 12px;
	margin: 0 7px 7px 0;
	padding: 7px;
}
@media (max-width: 1200px) {
	.profilecard{
		justify-content: space-around;
	}
}